import React from 'react';
import PropTypes from 'prop-types';
import { platform, IOS } from '@vkontakte/vkui';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import PanelHeaderButton from '@vkontakte/vkui/dist/components/PanelHeaderButton/PanelHeaderButton';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import FormLayout from '@vkontakte/vkui/dist/components/FormLayout/FormLayout';
import Textarea from '@vkontakte/vkui/dist/components/Textarea/Textarea';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import FormStatus from '@vkontakte/vkui/dist/components/FormStatus/FormStatus';
import Icon28ArrowLeftOutline from '@vkontakte/icons/dist/28/arrow_left_outline';

const osName = platform();

const Predlog = props => (
	<Panel id={props.id}>
		<PanelHeader
			left={<PanelHeaderButton onClick={props.go} data-to="home">
				{osName === IOS ? <Icon28ArrowLeftOutline/> : <Icon24Back/>}
			</PanelHeaderButton>}
		>
			Предложить 
		</PanelHeader>
		<FormLayout>
      <FormStatus header="Данную функцию тестировать не нужно!" >
        Данная функция ещё находится в разработке, тестировать её пока не нужно 
      </FormStatus>
	 </FormLayout>
		<FormLayout>
        <Textarea placeholder="Категория, название лайфхака, процесс изготовления" />
      </FormLayout>
	  <Div>
	  <Button size="xl" level="2">
					Отправить
				</Button>
				</Div>
	</Panel>
);

Predlog.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Predlog;
